<template>
	<view class="container">
		<view class="form-box">
			<view class="row">
				<view class="title">联系人</view>
				<input type="text" placeholder="请输入联系人" :value="info.name" class="input" @input="inputValue"
					data-label="name">
			</view>
			<view class="row">
				<view class="title">手机号</view>
				<input type="text" placeholder="请输入手机号" :value="info.mobile" class="input" @input="inputValue"
					data-label="mobile">
			</view>
			<view class="row">
				<view class="title">省份</view>
				<picker mode='selector' :range="provinceData" :value="provinceIndex" @change="provinceChang">
					<input type="text" placeholder="请选择省份" :value="info.province" class="input">
				</picker>
			</view>
			<view class="row">
				<view class="title">城市</view>
				<picker mode='selector' :range="cityData" :value="cityIndex" @change="cityChang" v-if="info.province">
					<input type="text" placeholder="请选择城市" :value="info.city" class="input">
				</picker>
				<input type="text" placeholder="请选择城市" disabled="true" class="input"
					@click="!info.province?areaClick(1):''" v-else>
			</view>
			<view class="row">
				<view class="title">区县</view>
				<picker mode='selector' :range="countyData" :value="countyIndex" @change="countyChang" v-if="info.city">
					<input type="text" placeholder="请选择区县" :value="info.county" class="input">
				</picker>
				<input type="text" placeholder="请选择区县" disabled="true" class="input" @click="!info.city?areaClick(2):''"
					v-else>
			</view>
			<view class="row">
				<view class="title">门牌号</view>
				<input type="text" placeholder="请输入门牌号" :value="info.address" class="input" @input="inputValue"
					data-label="address">
			</view>
			<view class="row switch-box">
				<view class="title">设为默认</view>
				<switch @change="switchChange" :checked="info.is_default" color="#009000" />
			</view>
			<button class="btn" @click="submit">提交</button>
			<button class="btn btn2" @click="deleteAddress">删除</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				info: '',
				provinceData: [],
				provinceIdData: [],
				provinceIndex: 0,
				cityData: [],
				cityIdData: [],
				cityIndex: 0,
				countyData: [],
				countyIdData: [],
				countyIndex: 0
			}
		},
		onLoad(options) {
			if (options.info) {
				this.info = JSON.parse(decodeURIComponent(options.info))
				let provinceId = this.info.province_id
				let cityId = this.info.city_id
				let countyId = this.info.county_id
				if (provinceId) {
					this.getProvinceData().then(res => {
						if (res) {
							this.provinceIdData.forEach((item, index) => {
								if (item == provinceId) {
									this.provinceIndex = index
								}
							})
						}
					})
					this.getCityData(provinceId).then(res => {
						if (res) {
							if (cityId) {
								this.cityIdData.forEach((item, index) => {
									if (item == cityId) {
										this.cityIndex = index
									}
								})
								this.getCountyData(cityId).then(res => {
									if (res) {
										if (countyId) {
											this.countyIdData.forEach((item, index) => {
												if (item == countyId) {
													this.countyIndex = index
												}
											})
										}
									}
								})
							} else {
								this.getCountyData(this.cityIdData[0])
							}
						}
					})
				} else {
					this.getProvinceData()
				}
			}
		},
		methods: {
			// 获取省份
			async getProvinceData() {
				const response = await this.$api.user.getProvinceData()
				if (response.data.code == 200) {
					this.provinceData = response.data.data.map((item) => {
						return item.name
					})
					this.provinceIdData = response.data.data.map((item) => {
						return item.pid
					})
					return true
				}
			},
			// 获取选中省份下的所有市
			async getCityData(provinceId) {
				const response = await this.$api.user.getCityData(provinceId)
				if (response.data.code == 200) {
					this.cityData = response.data.data.map((item) => {
						return item.name
					})
					this.cityIdData = response.data.data.map((item) => {
						return item.cid
					})
					return true
				}
			},
			// 获取选中市下的所有区县
			async getCountyData(cityId) {
				const response = await this.$api.user.getCountyData(cityId)
				if (response.data.code == 200) {
					this.countyData = response.data.data.map((item) => {
						return item.name
					})
					this.countyIdData = response.data.data.map((item) => {
						return item.yzid
					})
					return true
				}
			},
			// 修改收货地址数据
			async setAddressData(data) {
				const response = await this.$api.user.setAddressData(data)
				if (response.data.code == 200) {
					uni.showToast({
						title: '提交成功',
						icon: 'success'
					})
					let t = setTimeout((res) => {
						uni.navigateBack()
					}, 1000)
				}
			},
			// 选择省份
			provinceChang(e) {
				this.cityData = []
				this.cityIdData = []
				this.cityIndex = 0
				this.countyData = []
				this.countyIdData = []
				this.countyIndex = 0
				let index = e.detail.value
				let province = this.provinceData[index]
				let provinceId = this.provinceIdData[index]
				this.provinceIndex = index
				this.info.province = province
				this.info.province_id = provinceId
				this.info.city = ''
				this.info.city_id = ''
				this.info.county = ''
				this.info.county_id = ''
				this.getCityData(provinceId)
			},
			// 选择城市
			cityChang(e) {
				this.countyData = []
				this.countyIdData = []
				this.countyIndex = 0
				let index = e.detail.value
				let city = this.cityData[index]
				let cityId = this.cityIdData[index]
				this.cityIndex = index
				this.info.city = city
				this.info.city_id = cityId
				this.info.county = ''
				this.info.county_id = ''
				this.getCountyData(cityId)
			},
			// 选择区县
			countyChang(e) {
				let index = e.detail.value
				let county = this.countyData[index]
				let countyId = this.countyIdData[index]
				this.countyIndex = index
				this.info.county = county
				this.info.county_id = countyId
			},
			areaClick(type) {
				if (type == 1) {
					uni.showToast({
						title: '请先选择省份',
						icon: 'error'
					})
				} else {
					uni.showToast({
						title: '请先选择城市',
						icon: 'error'
					})
				}
			},
			//开关
			switchChange(e) {
				this.info.is_default = e.detail.value
			},
			inputValue(e) {
				this.info[e.currentTarget.dataset.label] = e.detail.value
			},
			submit() {
				let name = this.info.name
				let mobile = this.info.mobile
				let province = this.info.province
				let city = this.info.city
				let county = this.info.county
				let address = this.info.address
				let regMobile = /^1[3-9]\d{9}$/
				if (!name) {
					uni.showToast({
						title: '联系人不能为空',
						icon: 'error'
					})
				} else if (!mobile) {
					uni.showToast({
						title: '手机号不能为空',
						icon: 'error'
					})
				} else if (!regMobile.test(mobile)) {
					uni.showToast({
						title: '手机号格式错误',
						icon: 'error'
					})
				} else if (!province) {
					uni.showToast({
						title: '请选择省份',
						icon: 'error'
					})
				} else if (!city) {
					uni.showToast({
						title: '请选择城市',
						icon: 'error'
					})
				} else if (!county) {
					uni.showToast({
						title: '请选择区县',
						icon: 'error'
					})
				} else if (!address) {
					uni.showToast({
						title: '门牌号不能为空',
						icon: 'error'
					})
				} else {
					this.setAddressData(this.info)
				}
			},
			// 删除收货地址
			async deleteAddress(){
				let id = this.info.id
				const response = await this.$api.user.deleteAddress(id)
				if (response.data.code == 200) {
					uni.showToast({
						title: '删除成功',
						icon: 'success'
					})
					let t = setTimeout((res) => {
						uni.navigateBack()
					}, 1000)
				}
			}
		}
	}
</script>

<style lang="scss">
	.container {
		height: calc(100vh - 44px);
		background-color: #fff;

		.form-box {
			padding: 30upx;

			.row {
				display: flex;
				align-items: center;
				justify-content: flex-start;
				margin-bottom: 30upx;

				.title {
					width: 120upx;
					font-size: 28upx;
				}

				.input {
					flex: 1;
					font-size: 28upx;
					height: 70upx;
					border: 1px solid #F0F0F0;
					padding: 0 20upx;
				}

				picker {
					flex: 1;
				}
			}

			.switch-box {
				justify-content: space-between;
			}

			.btn {
				width: 100%;
				height: 80upx;
				line-height: 80upx;
				background-color: #009000;
				border-radius: 50upx;
				color: #fff;
				margin-top: 50upx;
				font-size: 35upx;
			}
			.btn2{
				margin-top: 30upx;
				background-color: $uni-error;
			}
		}
	}

	/* #ifdef MP-WEIXIN */
	.container {
		height: 100vh;
	}

	/* #endif */
</style>